.skillsBlock {
    height: 100vh;
    background-color: #b2d7b2;
}
.skillsContainer {
    /*тут перезапишем стили универсального контейнера - styleСontainer */
    flex-direction: column;
    justify-content: flex-start;
}
.title {
    background-color: #8c8787;
}
.skills {
    border: 1px solid;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}